// style go here
* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  max-width: fit-content;
  gap: 2rem;
  padding: 0 2.5rem;
  margin: auto;

  background-color: rgb(255, 160, 160);
  border-radius: 2rem;

  &__title {
    margin-top: 2.5rem;
    color: rgb(42, 50, 56);
  }
}

.controlls {
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem;
  border-radius: 2rem 2rem 0 0;
  font-size: 1rem;
  font-weight: bold;
  color: rgb(42, 50, 56);

  background-color: rgb(255, 160, 160);

  &__input {
    box-sizing: border-box;
    padding: 0.5rem;
    border: none;
    border-radius: 1rem;
  }
}
